.successNotif {
    border: solid;
    border-color: green;
    border-radius: 5px;
    color: green;
    background-color: lightgrey;
    font-size: medium;
    padding: 10px;
    margin-bottom: 10px;
}

.errorNotif {
    border: solid;
    border-color: red;
    border-radius: 5px;
    color: red;
    background-color: lightgrey;
    font-size: medium;
    padding: 10px;
    margin-bottom: 10px;
}